import React, { useState } from 'react';

import {
  MDBNavbar,
  MDBContainer,
  MDBIcon,
  MDBNavbarNav,
  MDBNavbarItem,
  MDBNavbarLink,
  MDBNavbarToggler,
  MDBNavbarBrand,
  MDBCollapse
} from 'mdb-react-ui-kit';
import 'mdb-react-ui-kit/dist/css/mdb.min.css';
import "@fortawesome/fontawesome-free/css/all.min.css";
import './App.css';

import logo from './logo.png';
import { Link } from "react-router-dom";

export default function App() {
  
  const [showNavColor, setShowNavColor] = useState(false);
  const [showNavColorSecond, setShowNavColorSecond] = useState(false);
  const [showNavColorThird, setShowNavColorThird] = useState(false);
 
  
  return (
    
    <>
   
      <MDBNavbar expand='lg' dark sticky  >
        <MDBContainer >
          <MDBNavbarBrand href='#' className='logo'><img src={logo}  width={"auto"} height={"auto"} alt={"logo"}/></MDBNavbarBrand>
          <MDBNavbarToggler
            type='button'
            data-target='#navbarColor02'
            aria-controls='navbarColor02'
            aria-expanded='false'
            aria-label='Toggle navigation'
            onClick={() => setShowNavColor(!showNavColor)}
            id='menu-icon'
          >
            <MDBIcon icon='bars' fas />
          </MDBNavbarToggler>
          <MDBCollapse show={showNavColor} navbar>
            <MDBNavbarNav className='m-auto mb-0 mt-0 mb-lg-0 w-auto'>
              <MDBNavbarItem>
                <MDBNavbarLink> <Link to="/">Overview</Link></MDBNavbarLink>
              </MDBNavbarItem>
              <MDBNavbarItem>
                <MDBNavbarLink href='#faq'>Features</MDBNavbarLink>
              </MDBNavbarItem>
              <MDBNavbarItem>
                <MDBNavbarLink href='#plan'>Plan</MDBNavbarLink>
              </MDBNavbarItem>
              <MDBNavbarItem>
                <MDBNavbarLink href='#contact'>Contact Us</MDBNavbarLink>
              </MDBNavbarItem>
            </MDBNavbarNav>
           
            <button href='#' className='quote'><span className='gredient-text'>Get Reeva</span></button>
            
          </MDBCollapse>
        </MDBContainer>
      </MDBNavbar>
      
    </>
  );
}
